<template>
  <!--  -->
  <div>
    <el-row>
      <el-col style="width:300px;">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label=":">
            <el-select v-model="unionId" clearable placeholder="" @change="search">
              <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col style="width:300px;">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label=":">
            <el-select v-model="toMemberId" clearable placeholder="" @change="search">
              <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col style="width:100px;">
        <div class="grid-content bg-purple">
          <el-select v-model="value" clearable placeholder="" class="fl">
            <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
      </el-col>
      <el-col style="width:200px;">
        <div class="grid-content1 bg-purple">
          <el-input placeholder="" @keyup.enter.native="search" icon="search" v-model="input" :on-icon-click="search" class="input-search2 fl">
          </el-input>
        </div>
      </el-col>
    </el-row>
    <!--  -->
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="opportunity.clientName" label="" min-width="100px">
      </el-table-column>
      <el-table-column prop="opportunity.clientPhone" label="" min-width="100px">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>: {{ scope.row.opportunity.clientPhone }}</p>
            <div slot="reference" class="name-wrapper">
              {{ scope.row.opportunity.clientPhone }}
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="opportunity.businessMsg" label="" min-width="100px">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>{{ scope.row.opportunity.businessMsg }}</p>
            <div slot="reference" class="name-wrapper">
              <span>{{ scope.row.opportunity.businessMsg }}</span>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="toMember.enterpriseName" label="()" min-width="135px">
      </el-table-column>
      <el-table-column prop="union.name" label="" min-width="100px">
      </el-table-column>
      <el-table-column prop="opportunity.acceptPrice" label="()" min-width="125">
      </el-table-column>
      <el-table-column prop="opportunity.brokerageMoney" label="()" min-width="120">
      </el-table-column>
      <el-table-column prop="opportunity.acceptStatus" label="" min-width="100px">
      </el-table-column>
      <el-table-column prop="isClose" label="" min-width="140px" :filters="[{ text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterTag" filter-placement="bottom-end">
        <template slot-scope="scope">
          <el-tag :type="scope.row.opportunity.isClose === '' ? 'danger' : 'success'">{{scope.row.opportunity.isClose}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="opportunity.createTime" label="" min-width="150">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>: {{ scope.row.opportunity.createTime }}</p>
            <div slot="reference" class="name-wrapper">
              {{ scope.row.opportunity.createTime }}
            </div>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll" v-if="tableData.length>0">
    </el-pagination>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
import { timeFilter } from '@/utils/filter.js';
import { commissionTypeFilter } from '@/utils/filter.js';
import { commissionIsCloseFilter } from '@/utils/filter.js';
export default {
  name: 'commission-income',
  data() {
    return {
      unionId: '',
      memberId: '',
      options1: [],
      toMemberId: '',
      options2: [],
      value: '',
      options3: [
        {
          value: 'clientName',
          label: ''
        },
        {
          value: 'clientPhone',
          label: ''
        }
      ],
      input: '',
      tableData: [],
      currentPage: 1,
      totalAll: 0
    };
  },
  computed: {
    initUnionId() {
      return this.$store.state.unionId;
    }
  },
  watch: {
    initUnionId: function() {
      this.init();
    },
    // 
    unionId: function() {
      this.toMemberId = '';
      this.options2 = [];
      if (this.unionId) {
        $http
          .get(`/unionMember/unionId/${this.unionId}/write/other`)
          .then(res => {
            if (res.data.data) {
              this.options2 = res.data.data || [];
              res.data.data.forEach((v, i) => {
                this.options2[i].value = v.id;
                this.options2[i].label = v.enterpriseName;
              });
            } else {
              this.options2 = [];
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
          });
      }
    },
  },
  mounted: function() {
    this.init();
  },
  methods: {
    init() {
      if (this.initUnionId) {
        // 
        $http
          .get(`/unionMain/my`)
          .then(res => {
            if (res.data.data) {
              this.options1 = res.data.data || [];
              this.options1.forEach((v, i) => {
                v.value = v.union.id;
                v.label = v.union.name;
              });
            } else {
              this.options1 = [];
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
          });
        $http
          .get(`/unionBrokerageIncome/opportunity/page?current=1`)
          .then(res => {
            if (res.data.data) {
              this.tableData = res.data.data.records || [];
              this.totalAll = res.data.data.total;
              this.tableData.forEach((v, i) => {
                v.opportunity.createTime = timeFilter(v.opportunity.createTime);
                v.opportunity.acceptStatus = commissionTypeFilter(v.opportunity.acceptStatus);
                v.opportunity.isClose = commissionIsCloseFilter(v.opportunity.isClose);
              });
            } else {
              this.tableData = [];
              this.totalAll = 0;
            }
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
          });
      }
    },
    // 
    search() {
      $http
        .get(
          `/unionBrokerageIncome/opportunity/page?current=1&unionId=${this.unionId}&toMemberId=${this.toMemberId}&` +
            this.value +
            '=' +
            this.input
        )
        .then(res => {
          if (res.data.data) {
              this.tableData = res.data.data.records || [];
              this.totalAll = res.data.data.total;
              this.tableData.forEach((v, i) => {
                v.opportunity.createTime = timeFilter(v.opportunity.createTime);
                v.opportunity.acceptStatus = commissionTypeFilter(v.opportunity.acceptStatus);
                v.opportunity.isClose = commissionIsCloseFilter(v.opportunity.isClose);
              });
            } else {
              this.tableData = [];
              this.totalAll = 0;
            }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    handleCurrentChange(val) {
      $http
        .get(
          `/unionBrokerageIncome/opportunity/page?current=${val}&unionId=${this.unionId}&toMemberId=${this
            .toMemberId}&` +
            this.value +
            '=' +
            this.input
        )
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records || [];
            this.tableData.forEach((v, i) => {
              v.opportunity.createTime = timeFilter(v.opportunity.createTime);
              v.opportunity.acceptStatus = commissionTypeFilter(v.opportunity.acceptStatus);
              v.opportunity.isClose = commissionIsCloseFilter(v.opportunity.isClose);
            });
          } else {
            this.tableData = [];
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    filterTag(value, row) {
      return row.opportunity.isClose === value;
    }
  }
};
</script>



