'use strict';
import axios from 'axios'
import {
  Message
} from 'element-ui';

// axios 
// axios.defaults.baseURL = 'http://113.106.199.74:7884';
// axios.defaults.baseURL = 'http://192.168.3.59:8080';
axios.defaults.baseURL = 'http://union.yifriend.net:7884';  // 
// axios.defaults.baseURL = 'http://union.duofee.com';         // 
// axios.defaults.baseURL = 'https://union.deeptel.com.cn';   // 
// axios.defaults.baseURL = 'http://nb.union.deeptel.com.cn'; // 

axios.defaults.timeout = 5000;

// http request 
axios.interceptors.request.use(
  config => {
    // 
    return config;
  },
  err => {
    return Promise.reject(err);
  }
);

// http response 
axios.interceptors.response.use(
  res => {
    // 
    return res;
  },
  err => {
    return Promise.reject(err);
  }
);

function checkStatus(res) {
  // loading
  // http
  if (res && (res.status === 200 || res.status === 304 || res.status === 400)) {
    return res;
    // data return res.data
  }
  // 
  return {
    status: -404,
    errorMsg: res.data.errorMsg
  }
}

function checkCode(res) {
  // code()
  if (res.status === -404) {
    Message({
      showClose: true,
      message: res.errorMsg,
      type: 'warning',
      duration: 5000
    });
  }
  if (res.data && (!res.data.success)) {
    Message({
      showClose: true,
      message: res.data.errorMsg,
      type: 'warning',
      duration: 5000
    });
  }
  return res;
}

export default {
  post(url, data) {
    return axios({
      method: 'post',
      url,
      data: data,
      timeout: 10000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'application/json; charset=UTF-8'
      }
    }).then(
      res => {
        return checkStatus(res);
      }
    ).then(
      res => {
        return checkCode(res);
      }
    ).then(
      res => {
        if (res.data.redirectUrl && res.data.redirectUrl !== '') {
          top.window.location = res.data.redirectUrl;
        }
        return res;
      }
    )
  },
  del(url, data) {
    return axios({
      method: 'delete',
      url,
      data: data,
      timeout: 10000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'application/json; charset=UTF-8'
      }
    }).then(
      res => {
        return checkStatus(res);
      }
    ).then(
      res => {
        return checkCode(res);
      }
    ).then(
      res => {
        if (res.data.redirectUrl && res.data.redirectUrl !== '') {
          top.window.location = res.data.redirectUrl;
        }
        return res;
      }
    )
  },
  put(url, data) {
    return axios({
      method: 'put',
      url,
      data: data,
      timeout: 10000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'application/json; charset=UTF-8'
      }
    }).then(
      res => {
        return checkStatus(res);
      }
    ).then(
      res => {
        return checkCode(res);
      }
    ).then(
      res => {
        if (res.data.redirectUrl && res.data.redirectUrl !== '') {
          top.window.location = res.data.redirectUrl;
        }
        return res;
      }
    )
  },
  get(url, params) {
    return axios({
      method: 'get',
      url,
      params, // get 
      timeout: 10000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest'
      }
    }).then(
      res => {
        return checkStatus(res);
      }
    ).then(
      res => {
        return checkCode(res);
      }
    ).then(
      res => {
        if (res.data.redirectUrl && res.data.redirectUrl !== '') {
          top.window.location = res.data.redirectUrl;
        }
        return res;
      }
    )
  }
}



